import { Dispatch, useCallback, useRef, useState } from 'react'

const getIdleTime = (prevTimeTaken: number, maxActivePercentage: number) =>
	Math.max((prevTimeTaken / maxActivePercentage) * 100 - prevTimeTaken, 0)

export function useResponsiveThrottle<T>(
	initialState: T | (() => T),
	maxActivePercentage = 50,
): [T, Dispatch<(prevState?: T) => T>] {
	const [state, _setState] = useState<T>(initialState)

	const latestTime = useRef(0)
	const cancelToken = useRef(-1)

	const setState: Dispatch<(prevState?: T) => T> = useCallback(
		(updateFn) => {
			const token = ++cancelToken.current

			setTimeout(() => {
				const start = Date.now()

				_setState((prevState) => {
					if (token !== cancelToken.current) {
						return prevState
					}

					const result = updateFn(prevState)

					latestTime.current = Date.now() - start

					return result
				})
			}, getIdleTime(latestTime.current, maxActivePercentage))
		},
		[maxActivePercentage],
	)

	return [state, setState]
}
